export function productListUtils({ element }) {
    const { settings } = element;
    const colDesktop = settings.itemsPerRow.desktop.value;
    const colTablet = settings.itemsPerRow?.tablet?.value ?? settings.itemsPerRow.desktop.value;
    const colMobile = settings.itemsPerRow?.mobile?.value ?? settings.itemsPerRow?.tablet?.value ?? settings.itemsPerRow.desktop.value;
    const spacingDesktop = settings.itemSpacing.desktop.value;
    const spacingTablet = settings.itemSpacing?.tablet?.value ?? settings.itemSpacing.desktop.value;
    const spacingMobile = settings.itemSpacing?.mobile?.value ?? settings.itemSpacing?.tablet?.value ?? settings.itemSpacing.desktop.value;
    const layoutClass = `xb-product-list-${settings.$layoutType}`;
    const autoplayAttr = settings.autoplayEnabled === 'yes' ? { 'xo-autoplay': `${settings.autoplayDelay.value * 1000}` } : {};
    return {
        colDesktop,
        colTablet,
        colMobile,
        spacingDesktop,
        spacingTablet,
        spacingMobile,
        layoutClass,
        autoplayAttr
    };
}
